  padding-right: 6px;
}
.arco-timeline-right .arco-timeline-item-vertical-right > .arco-timeline-item-dot-wrapper {
  right: 0;
  left: unset;
}
.arco-timeline-right .arco-timeline-item-vertical-right > .arco-timeline-item-content-wrapper {
  text-align: right;
  margin-left: 0;
  margin-right: 16px;
}
